import robbyImageUpload from '@/components/robby-image-upload/robby-image-upload.vue'
import mButton from '@/components/m-button.vue'
import {mapState} from 'vuex'
export default {
	data() {
		return {
			number:'0',
			// enableDel : false,
			// enableAdd : true,
			// enableDrag : false,
			// limitNumber: 4,
			imageData : [],
			btn:{
			type:"primary",
			value:'提交',
			},
			feedbacks:'',
		}
	},
	methods: {
		countNum(e){
			this.number = e.detail.cursor
		},
		addImage(e){
			const that = this
			uni.chooseImage({
				count: 1,
				success: function(res) {
					console.log(res)
					that.imageData.push({
						name: 'feedback' + that.imageData.length,
						uri: res.tempFilePaths[0]
					})
					console.log(that.imageData)
				}
			
			});
		},
		deleteImage(e){
			console.log(e)
		},
		commit(){
			if(this.feedbacks.length == 0){
				return uni.showToast({
					title:'请填写反馈意见',
					icon:'none'
				})
			}else if(this.feedbacks.length < 10){
				return uni.showToast({
					title:'反馈意见必须是10个字及以上',
					icon:'none'
				})
			}else{
				if(this.imageData.length <= 0){
					this.test.post('order/submit_feedback_notPhoto',{
						feedback:this.feedbacks
					}).then(res=>{
						console.log(res)
						if(res.statusCode == 200 && res.data.errorCode == 0){
							uni.showToast({
								title:'上传成功',
									
								});
							uni.switchTab({
								url:'../info'
								})
							}else{
								uni.showToast({
									title:'上传失败',
									icon:'none'
							})
						}
					}).catch(err=>{
						console.log(err)
					})
					}
				 else{
					console.log(this.imageData)
						const imgs = this.imageData.map((value, index) => {
							return {
								name: value.name,
								uri: value.uri
							}
						})
					console.log(imgs,this.feedbacks)  
						uni.uploadFile({
							url: 'http://dev.pjy.name:8180/api/bizcust/order/submit_feedback',
							files: imgs,
							formData: {
								feedback:this.feedbacks
							},
							success: function(res) {
								console.log(res)
								var data = JSON.parse(res.data)
								console.log(data)
								if(res.statusCode == 200 && data.errorCode == 0){
									uni.showToast({
										title:'上传成功',
										
									})
									  uni.switchTab({
										url:'../info'
									})
								}else{
									uni.showToast({
										title:'上传失败',
										icon:'none'
									})
								} 
							},
							header:{
								Token:this.Token
							}
						})
				 }
			}
			

		},
	},
	computed:{
		...mapState(["Token"])
	},
	components:{
		robbyImageUpload,
		mButton
	}
}
